.nav-social {
    display: flex;
    gap: 1rem;

    a {
        transition: all 0.2s;
        background: rgba(#828e9b, 0.2);
        border-radius: 50%;
        line-height: 0;

        &:hover {
            background: rgba(#828e9b, 0.4);
        }
    }

    .icon {
        font-size: 3.8rem;
    }
}
@media (max-width: 1299.98px) {
    .nav-social {
        .icon {
            font-size: 3rem;
        }
    }
}
