.nav-burger {
    position: fixed;
    z-index: 200;
    top: 5.5rem;
    bottom: 0;
    width: 100%;
    left: 0;
    background: #204a8f;
    padding: 3.5rem 0;
    overflow: auto;
    display: none;

    &.active {
        display: block;
    }

    &__group {
        margin-top: 3.6rem;

        &:first-child {
            margin-top: 0;
        }
    }

    ul {
    }

    li {
        margin: 1.8rem 0;
    }

    a {
        display: inline-flex;
        align-items: center;
        font-weight: 500;
        font-size: 1.8rem;
        line-height: calc(21 / 18);
        color: #fcfdff;
        gap: 0.5rem;

        .icon {
            font-size: calc(12 / 18) + em;
            stroke: currentColor;
        }
    }
}
