.brands {
    background: #fff;

    &__list {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 2.5rem 0;
        gap: 3rem;

        a {
            font-size: 0;
        }

        img {
            max-height: 5.5rem;
        }
    }
}

@media (max-width: 1299.98px) {
    .brands {
        &__list {
            img {
                max-height: 3rem;
            }
        }
    }
}
