.panel-docs {
    padding: 2.8rem 2.5rem;
    background: #ffffff;
    border-radius: 1.2rem;

    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 2rem;
    }

    &__search {
        width: 31.6rem;
    }

    &__body {
        display: flex;
        margin-top: 6rem;
        margin-bottom: 2.2rem;
    }

    &__left {
        padding-right: 6rem;
        padding-left: 1.7rem;
    }

    &__right {
        flex-grow: 1;
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .panel-docs {
        padding: 0;

        &__body {
            display: block;
            margin: 0;
        }

        &__left {
            padding: 0;
        }

        &__right {
            padding: 4.5rem 1.3rem 1.8rem 1.3rem;
        }
    }
}

