.card-category {
    padding: 2.5rem;
    background: #ffffff;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.25);
    border-radius: 0.5rem;

    &__name {
        margin-bottom: 2.2rem;
        margin-top: 1.5rem;
        font-weight: 500;
        font-size: 1.5rem;
        line-height: calc(17 / 15);
        color: #1a1919;
    }
}
