.card-catalog {
    background: #f9f9f9;
    border-radius: 1rem;

    &__gallery {
        background: #fff;
        padding: 2rem;
        border-radius: 1rem 1rem 0 0;
    }

    &__body {
        padding: 2rem 1.5rem;
        border-radius: 0 0 1rem 1rem;
    }

    &__code {
        margin-top: 1rem;
        display: flex;
        align-items: center;
        gap: 1rem;

        &__value {
            font-weight: 500;
            font-size: 1.4rem;
            line-height: calc(16 / 10);
            color: #1a1919;
        }
    }

    &__copy {
        margin-top: 1rem;
        display: flex;
        flex-wrap: wrap;
        gap: 1rem 1.5rem;

        button {
            font-size: 1rem;
        }
    }

    &__group {
        margin-top: 1.5rem;
    }

    &__name {
        font-weight: 600;
        font-size: 1.4rem;
        line-height: calc(16 / 14);
        color: #1a1919;
    }

    &__label {
        margin-bottom: 0.5rem;
        font-size: 1rem;
        line-height: calc(16 / 10);
        color: #9a9a9a;

        &--price {
            margin-bottom: 0;
        }
    }

    &__oems {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -0.4rem;
        gap: 0.8rem 0;

        > div {
            flex: 0 0 33.33%;
            padding: 0 0.4rem;

            span {
                display: block;
                background: #ffffff;
                border-radius: 1.5rem;
                padding: calc(4 / 10) + em;
                font-weight: 500;
                font-size: 1rem;
                line-height: calc(12 / 10);
                text-align: center;
                color: #525252;
                white-space: nowrap;
            }
        }
    }

    &__footer {
        margin-top: 1.5rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__price {
        font-weight: 500;
        font-size: 1.8rem;
        line-height: calc(27 / 18);
        color: #1a1919;
    }

    &__btn-where {
        button {
            padding-right: 1rem;
            padding-left: 1rem;
            font-size: 1rem;
        }
    }
}

@media (max-width: 1299.98px) {
    .card-catalog {
        &__gallery {
            padding: 2.4rem;
        }

        &__body {
            padding: 2.4rem;
            padding-top: 1.8rem;
        }

        &__name {
            font-size: 1.8rem;
        }

        &__oems {
            > div {
                span {
                    font-size: 1.2rem;
                }
            }
        }

        &__label {
            font-size: 1.4rem;

            &--price {
                text-transform: uppercase;
                font-size: 1rem;
                line-height: 1;
            }
        }

        &__price {
            font-size: 2.8rem;
            font-weight: 600;
        }
    }
}
