.l-points {
    &__map {
        margin: 4rem 0;
        height: 40.5rem;
    }

    &__filter {
        margin-top: 7.5rem;
        margin-bottom: 3rem;
    }

    &__list {
        &__scroll {
            height: auto;
        }
    }

    &__pages {
        margin-top: 5rem;
        margin-bottom: 7.5rem;
    }
}

@media (max-width: 1299.98px) {
    .l-points {
        &__pages {
            margin-top: 3rem;
            margin-bottom: 4rem;
            display: flex;
            justify-content: center;
        }
    }
}

@media (max-width: 599.98px) {
}
