.l-rules {
    &__cats {
        margin: 4rem 0;
    }

    &__main {
        display: flex;
        margin-bottom: 11.5rem;

        &__left {
            flex: 0 0 auto;
            width: 27.5rem;
        }

        &__right {
            flex: 0 0 auto;
            width: calc(100% - 27.5rem);
            padding-left: 4.4rem;
        }
    }

    &__panel {
        margin: 2.5rem 0;

        @include reset-margins;
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .l-rules {
        &__cats {
            margin-top: 1.5rem;
            margin-bottom: 2rem;
        }

        &__main {
            display: block;
            margin-bottom: 4rem;

            &__left {
                width: auto;
            }

            &__right {
                padding: 0;
                margin-top: 1.7rem;
                width: auto;
            }
        }

        &__panel {
            margin: 1.7rem 0;
        }
    }
}

