.list-news {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -1.5rem;
    gap: 2rem 0;

    &--3-cols {
        .list-news__col {
            width: 33.33%;
        }
    }

    &__col {
        flex: 0 0 auto;
        padding: 0 1.5rem;
        width: 50%;
        display: flex;

        > * {
            flex-grow: 1;
        }
    }
}

@media (min-width: 1300px) {
