.list-accordion {
    &__item {
        margin-top: 1rem;
        border-top: 2px solid #dedede;

        &:first-child {
            margin-top: 0;
        }
    }

    &__text {
        padding: 1rem 1.2rem;
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .list-accordion {
        &__item {
            margin-top: 0;

            &:last-child {
                border-bottom: 2px solid #dedede;
            }
        }

        &__text {
            padding: 1rem 0;
        }
    }
}

