.panel-tecdoc {
    min-height: 7.6rem;
    padding: 0.5rem 1.6rem;
    background: #ffffff;
    border-radius: 5px;
    display: inline-flex;
    align-items: center;
    gap: 3rem;

    &__text {
        font-weight: 500;
        font-size: 1.4rem;
        line-height: calc(16 / 14);
        color: #626262;
    }

    &__icon {
        width: 10.6rem;
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .panel-tecdoc {
        display: flex;
        padding: 1rem 1.8rem;

        &__icon {
            width: 12rem;
        }
    }
}

