.l-product {
    &__main {
        margin-top: 3rem;

        &__center {
            display: flex;
            margin-bottom: 5.7rem;
        }

        &__left {
            flex: 0 0 auto;
            width: 45%;
            background: #ffffff;
            border-radius: 1.5rem;
            padding: 3rem;
            align-self: flex-start;
        }

        &__right {
            flex: 0 0 auto;
            padding-left: 4rem;
            width: 55%;
        }
    }

    &__info {
        display: flex;
        margin-bottom: 3rem;

        &__left {
            flex-grow: 1;
            padding-right: 4rem;
        }

        &__right {
            flex: 0 0 auto;
            width: 26rem;
        }

        &__code {
            margin-bottom: 2.8rem;
            display: flex;
            align-items: center;
            gap: 2rem;
        }
    }

    &__props {
        &__header {
            margin-bottom: 1rem;
        }
    }

    &__match {
        &__models {
            margin: 3rem 0;
        }
    }

    &__extra {
        margin: 3rem 0 6rem 0;

        &__header {
            margin-bottom: 2.4rem;
        }

        &__scroll {
            height: 30.7rem;
        }

        &__footer {
            display: flex;
            justify-content: space-between;
            margin-top: 1.8rem;
        }

        &__attn {
            max-width: 57.6rem;
        }

        &__tecdoc {
            max-width: 30.6rem;
        }
    }

    &__docs {
        margin: 6rem 0;
    }

    &__goods {
        margin-bottom: 4.3rem;
        margin-top: 6rem;
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .l-product {
        &__main {
            &__center {
                display: block;
                margin-bottom: 4rem;
            }

            &__left {
                width: 100%;
            }

            &__right {
                width: 100%;
                padding: 0;
            }
        }

        &__info {
            display: block;
            margin: 0;

            &__left {
                padding: 0;
            }

            &__right {
                width: auto;
                margin-top: 2.7rem;
            }

            &__code {
                margin: 3rem 0;
            }
        }

        &__props {
            &__body {
                padding: 1.4rem 0;
            }
        }

        &__extra {
            &__footer {
                display: block;
                margin-top: 1.5rem;
            }

            &__tecdoc {
                margin-top: 0.8rem;
                max-width: none;
            }
        }

        &__apply {
            margin: 2.5rem 0;

            &__tip {
                margin: 3rem 0 2.4rem 0;
                font-size: 1.4rem;
                line-height: calc(20 / 14);
                color: #9a9a9a;
            }
        }

        &__oems {
            margin: 2.5rem 0;

            &__search {
                margin: 2.4rem 0;

                .input-search {
                    font-size: 1.6rem;
                }
            }
        }

        &__docs {
            margin: 2.5rem 0;

            &__search {
                margin: 3.5rem 0;

                .input-search {
                    font-size: 1.6rem;
                }
            }
        }

        &__goods {
            margin-top: 2.5rem;

            &__btn-toggle {
                margin-bottom: 2.8rem;
            }
        }
    }
}

