.panel-price {
    padding: 2.2rem 1.3rem;
    background: #ffffff;
    border-radius: 0.5rem;

    &__price {
        display: flex;
        justify-content: space-between;
        gap: 2rem;
        margin-bottom: 2.8rem;

        &__value {
            font-weight: 600;
            font-size: 2.8rem;
            line-height: calc(33 / 28);
            color: #204a8f;
            white-space: nowrap;
        }

        &__desc {
            font-weight: 500;
            font-size: 1.2rem;
            line-height: 120.3%;
            color: #7e7e7e;
        }
    }

    &__btns {
        &__item {
            margin-top: 1.2rem;

            &:first-child {
                margin-top: 0;
            }
        }
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .panel-price {
        &__btns {
            &__item {
                margin-top: 1.8rem;
            }
        }
    }
}

