.btn-filled {
    padding: calc(14 / 15) + em calc(24 / 15) + em;
    background: #204a8f;
    border-radius: 0.5rem;
    font-weight: 500;
    font-size: 1.5rem;
    line-height: calc(18 / 15);
    text-align: center;
    color: #ffffff;

    &:hover {
        background: darken(#204a8f, 5%);
    }

    &--gray-bg {
        background: #e8e9e9;
        color: #212529;

        &:hover {
            background: darken(#e8e9e9, 5%);
        }
    }

    &--size-1 {
        padding: calc(12 / 12) + em calc(15 / 12) + em;
        font-size: 1.2rem;
        font-weight: 400;
        line-height: 1;
    }

    &--size-2 {
        padding: calc(16 / 17) + em calc(20 / 17) + em;
        font-size: 1.7rem;
    }

    &--size-filter {
        font-size: 1.25rem;
    }
}

@media (max-width: 1299.98px) {
    .btn-filled {
        font-size: 1.3rem;

        &--size-2 {
            font-size: 1.4rem;
        }
    }
}

@media (max-width: 599.98px) {
}
