.form-default {
    &__item {
        margin-top: 1rem;

        @include reset-margins;
    }

    &__label {
        display: block;
        margin-bottom: 1rem;
        font-size: 1.4rem;
        color: #9a9a9a;

        span {
            color: #204a8f;
        }
    }

    &__btns {
        margin-top: 1.7rem;

        button {
            min-width: 16rem;
        }
    }
}
