.panel-404 {
    padding: 7rem 0 11rem 0;
    background: #ffffff;
    border-radius: 2.6rem;

    &__content {
        max-width: 74rem;
        margin: 0 auto;
    }

    &__icon {
        text-align: center;
        .icon {
            font-size: 14.8rem;
        }
    }

    &__title {
        margin-top: 3.7rem;
        font-weight: 500;
        font-size: 3.2rem;
        line-height: calc(36 / 32);
        text-align: center;
        color: #1a1919;
    }

    &__desc {
        margin-top: 2.2rem;
        font-weight: 400;
        font-size: 2rem;
        line-height: calc(26 / 20);
        text-align: center;
        color: #525252;
    }

    &__nav {
        margin-top: 7.5rem;
        display: flex;
        gap: 3.4rem;

        &__input {
            flex-grow: 1;
        }
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .panel-404 {
        margin-top: 5rem;
        padding: 4rem;
        flex-wrap: wrap;

        &__icon {
            .icon {
                font-size: 10rem;
            }
        }

        &__desc {
            font-size: 1.4rem;
        }

        &__title {
            font-size: 2rem;
        }

        &__nav {
            margin-top: 5rem;
            display: block;

            &__input {
                .input-search {
                    font-size: 1.4rem;
                }
            }

            &__btn {
                margin-bottom: 2rem;

                button {
                    width: 100%;
                }
            }
        }
    }
}

@media (max-width: 599.98px) {
    .panel-404 {
        padding: 3rem;
    }
}

