.popup-stories {
    position: fixed;
    z-index: 200;
    height: 100%;
    width: 100%;

    top: 0;
    left: 0;
    display: none;
    justify-content: center;
    align-items: center;

    &.active {
        display: flex;
    }

    &__overlay {
        @include fill;
        background: rgba(51, 51, 51, 0.8);
    }

    &__center {
        position: relative;
        flex: 0 0 auto;
        width: clamp(300px, 80vw, 50rem);
    }

    &__slider {
        background: #222;
        border-radius: 3rem;
        position: relative;
        overflow: hidden;

        &:before {
            content: " ";
            padding-top: 153.76%;
            display: block;
        }

        .swiper {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }

    &__btn-prev,
    &__btn-next {
        @include reset-button;
        position: absolute;
        z-index: 10;
        left: 50%;
        transform: translateX(-50%);
        background: #272727;
        border-radius: 50%;
        width: 5rem;
        height: 5rem;
        display: flex;
        justify-content: center;
        align-items: center;

        &:hover {
            background: darken(#272727, 5%);
        }

        &.swiper-button-disabled {
            visibility: hidden;
        }

        .icon {
            font-size: 1.6rem;
            fill: #cccccc;
        }
    }

    &__btn-prev {
        top: -10rem;
    }

    &__btn-next {
        bottom: -10rem;
    }

    &__btn-mute {
        @include reset-button;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        z-index: 10;
        bottom: 1.6rem;
        left: 2rem;
        width: 3.3rem;
        height: 3.3rem;

        .icon {
            font-size: 2.2rem;

            &:first-child {
                display: block;
            }

            &:last-child {
                display: none;
            }
        }

        &.active {
            .icon {
                &:first-child {
                    display: none;
                }

                &:last-child {
                    display: block;
                }
            }
        }
    }

    &__btn-share,
    &__btn-close {
        @include reset-button;
        position: absolute;
        z-index: 10;
        left: 100%;
        transform: translateX(2.8rem);
        display: flex;
        justify-content: center;
        align-items: center;
        width: 3.7rem;
        height: 3.7rem;
        background: #ffffff;
        border-radius: 50%;
        color: #204a8f;
    }

    &__btn-close {
        top: 0;

        .icon {
            font-size: 1.3rem;
            fill: currentColor;
        }
    }

    &__btn-share {
        bottom: 1.6rem;

        .icon {
            font-size: 2.3rem;
            fill: currentColor;
        }
    }
}

@media (max-height: 999.98px) {
    .popup-stories {
        &__center {
            width: calc((100vh - 30rem) * 0.65036);
        }
    }
}

@media (max-height: 999.98px) and (max-width: 1299.98px) {
    .popup-stories {
        &__center {
            width: calc((100vh - 20rem) * 0.65036);
        }
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .popup-stories {
        &__btn-close {
            position: fixed;
            top: 2rem;
            right: 2rem;
            transform: none;
            left: auto;
        }

        &__btn-share {
            position: fixed;
            bottom: 2rem;
            right: 2rem;
            transform: none;
            left: auto;
        }

        &__btn-prev,
        &__btn-next {
            width: 3.7rem;
            height: 3.7rem;
        }

        &__btn-prev {
            top: -6rem;
        }

        &__btn-next {
            bottom: -6rem;
        }
    }
}

