.nav-titles {
    ul {
        display: flex;
        gap: 2.4rem;
    }

    li {
        &:hover {
            a {
                color: darken(#c5c5c5, 5%);
            }
        }
        &.active {
            a {
                color: #000;
            }
        }
    }

    a {
        font-weight: 600;
        font-size: 3rem;
        line-height: calc(35 / 30);
        color: #c5c5c5;
    }
}
