.list-goods {
    &__header {
        margin-bottom: 5rem;
    }

    &__row {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -2.9rem;
        gap: 4.4rem 0;
    }

    &__col {
        flex: 0 0 auto;
        width: 25%;
        padding: 0 2.9rem;
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .list-goods {
        &__row {
            margin: 0 -1.3rem;
            gap: 2.7rem 0;
        }
        &__col {
            padding: 0 1.3rem;
            width: 50%;
        }
    }
}

@media (max-width: 599.98px) {
}

@media (max-width: 499.98px) {
    .list-goods {
        &__col {
            width: 100%;
        }
    }
}

@media (max-width: 359.98px) {
}
