:root {
    --side-padding: 2rem;
}

* {
    box-sizing: border-box;
}

html,
body {
    height: 100%;
}

html {
    font-size: 10px;
}

body {
    text-size-adjust: none;
    min-width: 320px;
    background: #f1f1f2;
    font-family: "TTHover", sans-serif;
    color: #000;
}

img {
    max-width: 100%;
}

img[data-src] {
    visibility: hidden;
}

a,
button {
    transition: all 0.2s;
}

a {
    text-decoration: none;
    color: currentColor;
}

.icon {
    width: 1em;
    height: 1em;
}

