.card-banner {
    position: relative;
    background: #204a8f;
    border-radius: 1.3rem;
    height: 36.5rem;
    padding: 3.2rem;

    &__image {
        position: absolute;
        width: 60%;
        max-width: 56rem;
        top: 0;
        bottom: 0;
        right: 0;
        background: no-repeat 50% 50% / contain;
    }
    &__title {
        position: relative;
        margin-top: 3.2rem;
        font-weight: 600;
        font-size: 3rem;
        line-height: calc(35 / 30);
        color: #ffffff;
    }
    &__btn {
        position: relative;
        margin-top: 5rem;
    }
}
