.form-subscribe {
    display: flex;
    align-items: center;

    &__label {
        padding-right: 4.7rem;
        white-space: nowrap;
        font-size: 1.9rem;
        line-height: calc(22 / 19);
        color: #1a1919;
    }

    &__row {
        display: flex;
        gap: 1.2rem;

        .input-default {
            flex: 1 0 17rem;

            input {
                padding: 1.2rem 0;
                background: transparent;
                border: transparent;
                font-size: 1.8rem;
                line-height: calc(20 / 18);

                &::placeholder {
                    color: #9a9a9a;
                }
            }
        }

        .btn-filled {
            font-size: 1.4rem;
        }
    }
}
