.slider-news {
    &__header {
        display: flex;
        align-items: center;
        margin-bottom: 3rem;
        padding-left: 2.5rem;
    }

    &__title {
        color: #000;
    }

    &__btns {
        margin-left: auto;
    }

    &__body {
        display: flex;
    }

    &__left {
        flex: 0 0 auto;
        padding-right: 6rem;
        width: 39rem;
    }

    &__right {
        flex: 0 0 auto;
        width: calc(100% - 39rem);
        overflow: hidden;
    }

    &__slider {
        margin: 0 -0.5rem;

        .swiper-wrapper {
        }

        .swiper-slide {
            width: 33.33%;
            padding: 0 0.5rem;
            height: auto;
            display: flex;

            > * {
                flex-grow: 1;
            }
        }
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .slider-news {
        &__header {
            margin-bottom: 3.5rem;
            padding-left: 1rem;
        }

        &__left {
            display: none;
        }

        &__right {
            width: 100%;
        }

        &__title {
            font-size: 2rem;
        }

        .swiper-slide {
            width: 50%;
        }
    }
}

@media (max-width: 599.98px) {
    .slider-news {
        &__right {
            overflow: visible;
        }
        &__slider {
            overflow: visible;

            .swiper-slide {
                width: 20rem;
            }
        }
    }
}

