.btn-toggle {
    gap: 2rem;
    padding: calc(15 / 16) + em calc(24 / 16) + em;
    background: #204a8f;
    border-radius: 0.5rem;
    font-weight: 500;
    font-size: 1.6rem;
    line-height: calc(19 / 16);
    text-align: center;
    color: #ffffff;

    &.opened {
        span {
            &:nth-child(1) {
                display: none;
            }

            &:nth-child(2) {
                display: block;
            }
        }

        .icon {
            transform: scaleY(-1);
        }
    }

    &:hover {
        background: darken(#204a8f, 5%);
    }

    span {
        margin-left: 1em;
        display: none;

        &:nth-child(1) {
            display: block;
        }
    }

    .icon {
        font-size: calc(12 / 16) + em;
        stroke: #ffffff;
    }
}
