.arrows {
    display: flex;
    align-items: center;
    gap: 2.8rem;

    &--has-count {
        gap: 0 !important;
    }

    &__count {
        flex: 0 0 auto;
        width: calc(70 / 20) + em;
        text-align: center;
        font-size: 2rem;
        line-height: calc(28 / 20);
        color: currentColor;
    }

    &__btn-prev,
    &__btn-next {
        @include reset-button;
        width: 1em;
        height: 1em;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #f9f9f9;
        box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.25);
        border-radius: 50%;
        font-size: 4.4rem;
        color: #204a8f;

        &.disabled,
        &.swiper-button-disabled {
            color: #8e8e8e;
            cursor: default;
        }

        .icon {
            font-size: calc(15 / 44) + em;
            stroke: currentColor;
            margin-left: 0.1em;
        }
    }

    &__btn-prev {
        .icon {
            margin-left: -0.1em;
        }
    }
}

@media (max-width: 1299.98px) {
    .arrows {
        gap: 1.6rem;

        &__btn-prev,
        &__btn-next {
            font-size: 2.7rem;
        }
    }
}
