.panel-guarantee {
    position: relative;
    background: #ffffff;
    border-radius: 0.5rem;
    padding: 2rem 2.4rem;
    min-height: 16.5rem;

    &__stamp {
        position: absolute;
        bottom: -1rem;
        right: -1rem;
        background: url(../img/warranty.svg) no-repeat 50% 50% / contain;
        width: 1em;
        height: 1em;
        font-size: 10rem;
    }

    &__title {
        position: relative;
        font-weight: 600;
        font-size: 2rem;
        line-height: calc(28 / 20);
    }

    &__desc {
        position: relative;
        font-size: 1.4rem;
        line-height: calc(20 / 14);
        margin-top: 1rem;
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .panel-guarantee {
        min-height: auto;

        &__desc {
            margin-top: 1.6rem;
            font-size: 1.5rem;
        }

        &__stamp {
            font-size: 11.5rem;
            bottom: auto;
            top: -1rem;
        }
    }
}

