.slider-preview {
    &__edge {
        width: 10%;
        position: absolute;
        z-index: 10;
        top: 0;
        height: 100%;
        display: none;
        pointer-events: none;

        &.active {
            display: block;
        }

        &--l {
            background: linear-gradient(to right, #fff, rgba(#fff, 0));
            left: 0;
        }

        &--r {
            background: linear-gradient(to left, #fff, rgba(#fff, 0));
            right: 0;
        }
    }

    &__main {
        position: relative;

        &__btns {
            display: none;
        }
    }

    &__tape {
        position: relative;
        margin-top: 4.5rem;

        .swiper-slide {
            margin-right: 1.3rem;
            width: 33.33%;

            &:first-child {
                .slider-preview__image {
                    border-radius: 1rem 0 0 1rem;
                }
            }

            &:last-child {
                margin-right: 0;

                .slider-preview__image {
                    border-radius: 0 1rem 1rem 0;
                }
            }
        }

        .swiper-slide-thumb-active {
            .slider-preview__image {
                box-shadow: inset 0 0 0 2px #204a8f;
            }
        }
    }

    &__prev,
    &__next {
        display: none;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        z-index: 20;
        padding: 1em;
        border: 0;
        cursor: pointer;
        background: #fff;
        font-size: 1rem;

        &:hover {
            background: darken(#fff, 2%);
        }
    }

    &__prev {
        left: 0;
    }

    &__next {
        right: 0;
    }

    &__image {
        background: 50% 50% / cover no-repeat;

        &:before {
            content: " ";
            padding-top: 80%;
            display: block;
        }

        &--tape {
            &:before {
                padding-top: 31%;
            }
        }
    }
}

@media (max-width: 899.98px) {
    .slider-preview {
        &__main {
            &__btns {
                display: block;
            }
        }

        &__tape {
            &__btns {
                display: none;
            }
        }
    }
}
