.popup-markets {
    position: relative;
    max-width: 60rem;
    background: #ffffff;
    border-radius: 1rem;
    padding: 1.8rem 0;
    margin: 0 auto;

    &__header {
        border-bottom: 4px solid #f9f9f9;
        padding: 0 3.4rem 1.6rem 3.4rem;
    }

    &__title {
        font-weight: 600;
        font-size: 2.5rem;
        line-height: calc(29 / 25);
    }

    &__region {
        margin-top: 2rem;
        font-size: 1.6rem;
        line-height: calc(19 / 16);
        color: #646464;
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .popup-markets {
        &__header {
            padding-right: 2rem;
            padding-left: 2rem;
        }

        &__title {
            font-size: 1.9rem;
        }

        &__region {
            margin-top: 0.5rem;
        }
    }
}

