.input-search {
    position: relative;
    font-size: 1.6rem;

    input {
        width: 100%;
        padding: calc(12 / 16) + em calc(19 / 16) + em;
        padding-right: 4em;
        border: 1px solid #204a8f;
        border-radius: 0.5rem;
        font-family: inherit;
        font-weight: 500;
        font-size: 1em;
        line-height: calc(20 / 16);
        color: currentColor;
        outline: none;

        &::placeholder {
            color: #204a8f;
        }
    }

    .icon {
        position: absolute;
        top: 50%;
        right: calc(23 / 16) + em;
        transform: translateY(-50%);
        font-size: calc(17 / 16) + em;
        fill: #204a8f;
    }

    &--gray {
        input {
            border-color: #e9e9e9;

            &::placeholder {
                color: #9a9a9a;
            }
        }

        .icon {
            fill: #9a9a9a;
        }
    }

    &--transparent {
        input {
            background: transparent;
        }
    }
}

@media (max-width: 1299.98px) {
    .input-search {
        font-size: 1.2rem;
    }
}

