.card-video {
    position: relative;
    background: #c7c7c7 no-repeat 50% 50% / cover;
    border-radius: 1rem;
    overflow: hidden;
    display: flex;

    &:before {
        content: " ";
        padding-top: 46.43%;
        display: block;
    }

    &__btn-play {
        transition: all 0.2s;
        width: 1em;
        height: 1em;
        font-size: 6rem;
        position: absolute;
        top: 50%;
        left: 50%;
        background: url(../img/icons/play.svg) no-repeat 50% 50% / contain;
        margin: -0.5em 0 0 -0.5em;

        &:hover {
            transform: scale(1.3);
        }
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .card-video {
        &::before {
            padding-top: 66%;
        }

        &__btn-play {
            font-size: 3rem;
        }

        &--becomes-blue {
            border: 2px solid #204a8f;
            border-radius: 0.5rem;

            .card-video__btn-play {
                background-image: url(../img/icons/play-blue.svg);
                font-size: 3rem;
            }
        }
    }
}

