.form-contact {
    position: relative;
    background: #fafafa;
    box-shadow: 0px 17px 16px rgba(235, 241, 245, 0.55);
    border-radius: 1.5rem;
    padding: 3rem 1.3rem;

    &__desc {
        max-width: 25rem;
        font-size: 1.4rem;
        line-height: calc(20 / 14);
        color: #000000;

        p {
            margin-top: 1.6rem;

            @include reset-margins;
        }

        a {
            text-decoration: underline;
        }
    }

    &__list {
        margin-top: 2.5rem;
    }

    &__item {
        margin-top: 0.7rem;

        @include reset-margins;
    }

    &__btn {
        margin-top: 1.3rem;

        button {
            min-width: 14rem;

            &:nth-child(2) {
                display: none;
            }
        }
    }

    &__btn-toggle {
        @include reset-button;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        height: 5.7rem;
        width: 7.6rem;
        top: 1rem;
        transform: translateX(100%);
        right: 0;
        background: #ffffff;
        border-radius: 0px 1.5rem 1.5rem 0px;
    }
}

@media (max-width: 1299.98px) {
    .form-contact {
        padding: 2.4rem;
        display: flex;
        justify-content: flex-end;
        flex-direction: column;

        &__desc {
            font-size: 1.5rem;
        }

        &__btn-toggle {
            display: none;
        }

        &__btn {
            display: flex;
            gap: 2rem;

            > button {
                display: block !important;
                min-width: 0;
                flex-grow: 1;
            }
        }
    }
}

