.card-section {
    display: flex;
    flex-direction: column;
    background: #ffffff;
    border-radius: 1rem;
    overflow: hidden;
    height: 100%;

    &__image {
        background: no-repeat 100% 0 / contain;
        flex-basis: 20rem;
        flex-grow: 1;
    }

    &__name {
        padding: 1.7rem;
        padding-bottom: 2.5rem;
        font-weight: 500;
        font-size: 3rem;
        font-variant: small-caps;
        color: #204a8f;
    }
}
