.input-page {
    position: relative;
    font-size: 1.3rem;

    &__label {
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        font-size: 1em;
        line-height: calc(16 / 13);
        color: #9a9a9a;
    }

    input {
        width: 100%;
        padding: calc(7 / 13) + em 0;
        padding-left: 1.5em;
        border: 1px solid transparent;
        border-bottom-color: #9a9a9a;
        background: transparent;
        font-family: inherit;
        font-size: 1em;
        line-height: calc(17 / 13);
        color: #000;
        outline: none;
    }
}
