.nav-sections {
    background: #ffffff;
    border-radius: 1rem;
    padding: 2.7rem 0.6rem;

    ul {
    }
    li {
        &.active {
            a {
                background: #f1f1f2;
                color: #204a8f;
            }
        }
    }

    &__link {
        display: flex;
        gap: 1rem;
        padding: 2rem;
        font-weight: 500;
        font-size: 1.8rem;
        line-height: calc(20 / 18);
        color: #525252;

        &--collapsible {
            &:after {
                transition: all 0.2s;
                content: " ";
                flex: 0 0 auto;
                display: block;
                width: 1em;
                height: 1em;
                background: #204a8f;
                mask: url(../img/icons/arrow-down.svg) no-repeat 50% 50% /
                    contain;
                align-self: center;
                margin-left: auto;
            }

            &.opened {
                &:after {
                    transform: rotate(180deg);
                }
            }
        }

        .icon {
            flex: 0 0 auto;
            font-size: calc(22 / 18) + em;
            fill: #204a8f;
        }

        &:hover {
            background: #f1f1f2;
            border-radius: 1rem;
            color: #204a8f;
        }
    }

    &__sub {
        display: flex;
        flex-direction: column;
        gap: 2rem;
        padding: 1rem;

        a {
            font-size: 1.5rem;
            color: #1a1919;

            span {
                color: #afafaf;
            }
        }
    }
}

@media (min-width: 1300px) {
    .nav-sections {
        &__link {
            &--collapsible {
                &:after {
                    display: none;
                }
            }
        }

        &__sub {
            display: none;
        }
    }
}

@media (max-width: 1299.98px) {
    .nav-sections {
        padding: 0.8rem;
        border-radius: 0.5rem;

        &__link {
            padding: 2rem 1rem;
        }
    }
}

