.list-stories {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -1.7rem;
    gap: 6.6rem 0;

    &__col {
        flex: 0 0 auto;
        padding: 0 1.7rem;
        width: 20%;
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .list-stories {
        margin: 0 -1rem;
        gap: 4rem 0;

        &__col {
            padding: 0 1rem;
            width: 33.33%;
        }
    }
}

@media (max-width: 599.98px) {
    .list-stories {
        &__col {
            width: 50%;
        }
    }
}

