.form-search {
    padding: 1.7rem 2.4rem;
    border-radius: 1rem;
    background: #ffffff;

    &__mobile {
        display: none;
    }

    &__desktop {
        &__input {
            margin-bottom: 1.7rem;
        }

        &__label {
            margin-bottom: 1.2rem;
            font-weight: 500;
            font-size: 1.6rem;
            line-height: calc(20 / 16);
            color: #757575;
        }
    }

    &__filter {
        &__row {
            display: flex;
        }
    }

    &__select {
        flex: 1 0 auto;
        padding-right: 1.5rem;
        width: 20%;
    }

    &__button {
        button {
            min-width: 10.7rem;
        }
    }
}

@media (min-width: 1300px) {
    .form-search {
        &__filter {
            &__collapse {
                padding-top: 0;
            }
        }
    }
}

@media (max-width: 1299.98px) {
    .form-search {
        padding: 0;
        background: none;

        &__mobile {
            display: block;
            background: #ffffff;
            box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
            border-radius: 0.5rem;
            padding: 1.5rem;
            margin-bottom: 1.2rem;

            &__label {
                margin-bottom: 1rem;
                font-weight: 500;
                font-size: 1.3rem;
                line-height: calc(20 / 13);
                color: #757575;
            }
        }

        &__desktop {
            display: none;
        }

        &__select {
            width: auto;
            margin-bottom: 1rem;
            padding: 0;
        }

        &__filter {
            &__row {
                display: block;
                padding-top: 1.2rem;
            }
        }
    }
}
