.input-default {
    font-size: 1.6rem;

    input,
    textarea {
        width: 100%;
        padding: calc(9 / 16) + em calc(19 / 16) + em;
        border: 1px solid #a0a5b9;
        border-radius: 0.5rem;
        font-family: inherit;
        font-weight: 500;
        font-size: 1em;
        line-height: calc(20 / 16);
        color: currentColor;
        outline: none;

        &::placeholder {
            color: #204a8f;
        }
    }

    &--blue-border {
        input,
        textarea {
            border-color: #204a8f;
            font-weight: 400;

            &::placeholder {
                color: #a0a5b9;
            }
        }
    }

    &--size-1 {
        input,
        textarea {
            padding: 0.7rem 1.6rem;
            font-size: 1.2rem;
            line-height: calc(20 / 12);
        }
    }
}
