.nav-bread {
    margin-top: 2.2rem;
    margin-bottom: 2.5rem;
    font-size: 1.5rem;
    line-height: calc(16 / 15);
    color: #1a1919;

    ul {
        display: flex;
        flex-wrap: wrap;
    }

    li {
        display: inline-flex;
        align-items: center;

        &:after {
            content: " ";
            width: 0.5rem;
            height: 0.5rem;
            background: #214076;
            border-radius: 50%;
            margin: 0 1rem;
        }

        &:last-child {
            &:after {
                display: none;
            }
        }
    }
}

@media (max-width: 1299.98px) {
    .nav-bread {
        margin: 1.5rem 0;
        font-size: 1.6rem;
    }
}
