.list-catalog {
    display: flex;
    margin: 0 -2.5rem;
    flex-wrap: wrap;
    gap: 3.4rem 0;

    &--4-items {
        .list-catalog__col {
            width: 25%;
        }
    }

    &__col {
        padding: 0 2.5rem;
        flex: 0 0 auto;
        width: 33.33%;
    }
}

@media (max-width: 1299.98px) {
    .list-catalog {
        margin: 0 -1.3rem;
        gap: 2.6rem 0;

        &__col {
            padding: 0 1.3rem;
            width: 50% !important;
        }
    }
}

@media (max-width: 599.98px) {
    .list-catalog {
        &__col {
            width: 100% !important;
        }
    }
}
