.header {
    padding-top: 4.4rem;

    &__center {
        padding: 1.3rem 2.3rem;
        border-radius: 6.1rem;
        background: #ffffff;
        display: flex;
        align-items: center;
    }

    &__logo {
        flex: 0 0 auto;
        width: 19.9rem;
    }

    &__nav {
        padding-left: 5.7rem;
        flex-grow: 1;
        display: flex;
        justify-content: space-between;
        gap: 4.5rem;

        &__group {
        }

        ul {
            display: flex;
            gap: 1.7rem;
        }

        li {
        }

        a {
            font-weight: 500;
            font-size: 1.5rem;
            line-height: calc(18 / 15);
            white-space: nowrap;
            color: #214076;
        }
    }

    &__star {
        padding: 0 1.8rem;
    }

    &__catalog {
        button {
            min-width: 12.7rem;
        }
    }

    &__burger {
        display: none;
    }
}

@media (max-width: 1299.98px) {
    .header {
        padding: 0;
        background: #fff;

        &__center {
            padding: 0;
            height: 5.5rem;
        }

        &__logo {
            width: 12rem;
            margin-right: auto;
        }

        &__nav {
            display: none;
        }

        &__catalog {
            button,
            a {
                min-width: auto;
                font-size: 1.3rem;
            }
        }

        &__burger {
            display: block;
            margin-left: 1.7rem;
        }
    }
}

@media (max-width: 359.98px) {
    .header {
        &__catalog {
            display: none;
        }

        &__burger {
            margin-left: 0;
        }
    }
}
