.btn-reset {
    padding: calc(11 / 15) + em calc(24 / 15) + em;
    border-radius: 0.5rem;
    font-weight: 500;
    font-size: 1.5rem;
    line-height: calc(18 / 15);
    text-align: center;
    color: #4a4a4a;

    &:hover {
        background: #f3f3f3;
    }

    &--size-filter {
        font-size: 1.25rem;
    }
}
