.btn-copy-lg {
    padding: calc(17 / 17) + em calc(14 / 17) + em;
    border: 2px solid #204a8f;
    border-radius: 0.5rem;
    font-weight: 500;
    font-size: 1.7rem;
    line-height: calc(20 / 17);
    color: #204a8f;

    b {
        font-weight: bold;
    }

    .icon {
        margin-left: calc(10 / 18) + em;
        font-size: calc(18 / 17) + em;
        fill: currentColor;
    }
}

