.panel-filter {
    padding: 1.4rem;
    background: #ffffff;
    border-radius: 0.5rem;

    &__group {
        margin-top: 1.7rem;

        @include reset-margins;
    }

    &__title {
        margin-bottom: 1.1rem;
        font-weight: 500;
        font-size: 1.3rem;
        line-height: calc(16 / 13);
    }

    &__btn-collapse {
        margin-top: 1.7rem;
    }

    &__footer {
        margin-top: 2.7rem;
        display: flex;
        flex-direction: column;
        gap: 0.5rem;
    }
}
