.table-points {
    width: 100%;
    min-width: 100rem;

    th,
    td {
        padding: 1.6rem;
        text-align: left;
        font-size: 1.6rem;
        line-height: calc(24 / 16);
        color: #2e2a25;
    }

    th {
        color: #62646a;
    }

    a {
        font-weight: 500;
        color: #204a8f;
    }
}

