.list-contacts {
    div {
        margin: 0.6rem 0;
        display: flex;
        align-items: center;
        gap: 1.2rem;
        font-size: 1.6rem;
        line-height: calc(24 / 16);
        color: #3d3f49;

        @include reset-margins;

        .icon {
            font-size: calc(24 / 16) + em;
            fill: #62646a;
        }
    }
}
