.footer {
    padding-top: 4.3rem;
    padding-bottom: 4.8rem;
    background: #204a8f;

    &__center {
        display: flex;
    }

    &__left {
        display: flex;
    }

    &__right {
        padding-right: 2.5rem;
    }

    &__col-about {
        padding-right: 5.3rem;
    }

    &__col-contacts {
        padding-right: 6.1rem;
    }

    &__col-info {
        padding-right: 6.8rem;
    }

    &__logo {
        width: 22.5rem;
    }

    &__desc {
        margin-top: -2rem;
        font-size: 1rem;
        line-height: calc(14 / 10);
        color: #828e9b;
    }

    &__phone {
        margin-top: 2rem;
        font-weight: 500;
        font-size: 1.6rem;
        line-height: calc(19 / 16);
        color: #ffffff;
        display: flex;
        align-items: center;
        gap: 1.2rem;

        .icon {
            font-size: 1em;
        }
    }

    &__social {
        margin-top: 5.4rem;
    }

    &__nav {
        margin-top: 2rem;

        flex-grow: 1;

        ul {
            display: flex;
            flex-wrap: wrap;
        }

        li {
            margin: 0 2.8rem 1.5rem 0;
        }

        a {
            font-weight: 500;
            font-size: 1.3rem;
            line-height: calc(19 / 13);
            color: #f9f9f9;
        }
    }

    &__year {
        width: 10.7rem;
    }

    &__tecdoc {
        margin-top: 4.1rem;
        display: flex;
        align-items: center;
        gap: 3.2rem;

        &__logo {
            width: 11.9rem;
        }

        &__desc {
            font-size: 1.3rem;
            line-height: calc(16 / 13);
            color: #ffffff;
        }
    }
}

@media (max-width: 1299.98px) {
    .footer {
        padding: 2rem 0;

        &__left {
            display: block;
        }

        &__right {
            margin-left: auto;
            padding-right: 1rem;
        }

        &__col-info {
            display: none;
        }

        &__col-about,
        &__col-contacts {
            padding-right: 2rem;
        }

        &__phone {
            display: none;
        }

        &__social {
            margin-top: 2.3rem;
        }

        &__desc {
            margin-top: 1.7rem;
        }

        &__year {
            width: 6.5rem;
        }

        &__logo {
            width: 16rem;
        }
    }
}
