.checkbox {
    display: block;
    position: relative;
    padding-left: 2.8rem;
    margin-bottom: 1rem;
    font-size: 1.3rem;
    line-height: calc(16 / 13);
    color: #1a1919;
    user-select: none;
    cursor: pointer;

    @include reset-margins;
}

.checkbox input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.checkmark {
    transition: all 0.2s;
    position: absolute;
    top: 0;
    left: 0;
    height: 2rem;
    width: 2rem;
    background: #f6f6f6;
    border-radius: 0.4rem;
}

.checkbox:hover input ~ .checkmark {
    background-color: #f5f5f5;
}

.checkbox input:checked ~ .checkmark {
}

.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

.checkbox input:checked ~ .checkmark:after {
    display: block;
}

.checkbox .checkmark:after {
    left: 37%;
    top: 11%;
    width: 20%;
    height: 50%;
    border: solid #000;
    border-width: 0 2px 2px 0;
    transform: rotate(45deg);
}
