.panel-tools {
    background: #f5f5f5;
    border-radius: 9px;
    padding: 2.4rem 3rem;
    display: flex;
    align-items: center;

    &__share {
        display: flex;
        align-items: center;
        gap: 3rem;

        &__label {
            font-size: 1.8rem;
            color: #9a9a9a;
        }
    }

    &__form {
        padding-left: 4.7rem;
        flex-grow: 1;
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .panel-tools {
        padding: 0;
        background: none;

        &__form {
            display: none;
        }
    }
}

