.l-index {
    &__main {
        margin-top: 4rem;

        &__center {
            display: flex;
        }

        &__left {
            flex: 0 0 auto;
            width: calc(100% - 37rem);
            padding-right: 4rem;
            display: flex;
            justify-content: space-between;
            flex-direction: column;
        }

        &__right {
            flex: 0 0 auto;
            width: 37rem;
        }
    }

    &__products {
        margin: 8rem 0;
    }

    &__catalog {
        margin-top: 8rem;
        margin-bottom: 9rem;
    }

    &__stories {
        margin-top: 8rem;
        margin-bottom: 16.5rem;
    }
}

@media (max-width: 1299.98px) {
    .l-index {
        &__main {
            &__center {
                flex-direction: column-reverse;
            }

            &__right {
                width: auto;
            }

            &__left {
                width: auto;
                margin-top: 2rem;
                padding: 0;
            }

            &__slider {
                display: none;
            }
        }
    }
}

@media (max-width: 1299.98px) {
    .l-index {
        &__main {
            margin-top: 1.5rem;
        }

        &__products {
            margin: 2.5rem 0 0 0;
        }

        &__catalog {
            margin: 5rem 0;
        }

        &__news {
            margin-top: 5.3rem;
            margin-bottom: 5rem;
        }

        &__stories {
            margin-bottom: 6.2rem;
            margin-top: 5rem;
        }
    }
}
