.nav-tags {
    ul {
        display: flex;
        flex-wrap: wrap;
        gap: 1rem 1.6rem;
    }

    li {
        &.active {
            a {
                border-color: #204a8f;
                color: #204a8f;
            }
        }
    }

    a {
        display: block;
        padding: 0.3rem 1.2rem;
        border: 1px solid #bdc2c7;
        border-radius: 0.4rem;
        font-size: 1.4rem;
        line-height: 1;
        color: #8a8c93;

        &:hover {
            border-color: darken(#bdc2c7, 10%);
        }
    }
}
