.btn-header-2 {
    width: 100%;
    padding: 1.2rem;
    background: #fff;
    justify-content: space-between;
    font-weight: 600;
    font-size: 2rem;
    line-height: calc(24 / 20);
    color: #204a8f;

    &.opened {
        .icon {
            transform: rotate(270deg);
            stroke: #b9b9b9;
        }
    }

    .icon {
        transition: all 0.2s;
        transform: rotate(90deg);
        margin: calc(5 / 15) + em;
        font-size: calc(22 / 20) + em;
        stroke: #204a8f;
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .btn-header-2 {
        background: none;
        padding: 1.2rem 0;
        font-size: 1.6rem;
    }
}

