.card-news {
    padding: 2rem;
    background: #fff;
    border-radius: 0.8rem;

    &__header {
        margin-top: 3.1rem;
        font-weight: 600;
        font-size: 1.5rem;
        line-height: calc(21 / 18);
        text-transform: uppercase;
        display: flex;
        flex-wrap: wrap;
        gap: 1rem 2rem;
    }

    &__tag {
        color: #204a8f;
    }

    &__date {
        color: #a0a5b9;
    }

    &__title {
        margin-top: 1.4rem;
        font-weight: 600;
        font-size: 2rem;
        line-height: calc(24 / 20);
        color: #333333;
    }
}

@media (max-width: 1299.98px) {
    .card-news {
        padding: 1.6rem;

        &__header {
            margin-top: 2.7rem;
            font-size: 1.3rem;
        }

        &__title {
            margin-top: 1rem;
            font-size: 1.5rem;
        }
    }
}
