.card-map {
    position: absolute;
    width: 332px;
    font-size: 1.8rem;
    line-height: calc(24 / 18);
    color: #ffffff;

    &--static {
        position: relative;
    }

    &__body {
        background: #204a8f;
        border-radius: 0.5rem;
        padding: 1.6rem;
    }

    &__title {
    }

    &__desc {
        margin-top: 1rem;
    }

    &__close {
        @include reset-button;
        position: absolute;
        right: 0;
        top: 0;
        padding: 1rem;
        font-size: 3rem;
        line-height: 0.5;
        color: #fff;
    }
}
