.list-radiator {
    &__item {
        margin-top: 2rem;
        background: #f8f8f8;
        box-shadow: 0 0 5px rgba(#000, 0.1);
        border-radius: 0.5rem;
        padding: 1rem;
        display: flex;
        align-items: center;
        gap: 1rem;

        @include reset-margins;
    }

    &__icon {
        flex: 0 0 auto;
        width: 14rem;
    }

    &__text {
        font-size: 1.3rem;
        line-height: calc(18 / 13);

        b {
            font-weight: 600;
        }
    }
}
