.card-faq {
    &__title {
        border-bottom: 1px solid #bdc2c7;
        padding-bottom: 0.5rem;
        font-size: 1.7rem;
        line-height: calc(28 / 17);
        color: #000000;
    }

    &__tags {
        margin-top: 2.2rem;
        margin-bottom: 1.3rem;
    }

    &__stat {
        display: flex;
        font-size: 1.4rem;
        line-height: calc(22 / 14);
    }

    &__date {
        margin-right: 2.3rem;
        color: #a0a5b9;
    }

    &__views {
        margin-right: 4rem;
        display: flex;
        align-items: center;
        gap: 0.4rem;
        color: #a0a5b9;

        .icon {
            font-size: 1em;
            fill: currentColor;
        }
    }

    &__reply {
        color: #204a8f;
    }
}

