.l-news {
    &__cats {
        margin: 5rem 0;
    }

    &__main {
        display: flex;
        margin: 0 -1.5rem;

        &__left {
            flex: 0 0 auto;
            padding: 0 1.5rem;
            width: 66.66%;
        }

        &__right {
            flex: 0 0 auto;
            padding: 0 1.5rem;
            width: 33.33%;
        }
    }

    &__pages {
        margin-top: 4.5rem;
        margin-bottom: 10.5rem;
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .l-news {
        &__cats {
            margin-top: 1.5rem;
            margin-bottom: 2.7rem;
        }

        &__main {
            display: block;

            &__left {
                width: auto;
            }

            &__right {
                display: none;
            }
        }

        &__pages {
            margin-top: 4rem;
            margin-bottom: 5.5rem;
        }
    }
}

