.input-search-underline {
    position: relative;
    font-size: 1.6rem;

    input {
        width: 100%;
        padding: calc(12 / 16) + em 0;
        padding-right: 2em;
        border: 1px solid transparent;
        border-bottom-color: #afafaf;
        font-family: inherit;
        font-weight: 500;
        font-size: 1em;
        line-height: calc(20 / 16);
        color: currentColor;
        outline: none;
        background: transparent;

        &::placeholder {
            color: #9a9a9a;
        }

        &:focus {
            border-bottom-color: darken(#afafaf, 15%);
        }
    }

    .icon {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        font-size: calc(17 / 16) + em;
        fill: #204a8f;
    }
}

@media (max-width: 1299.98px) {
    .input-search-underline {
        font-size: 1.4rem;
    }
}

@media (max-width: 599.98px) {
