.section-pdf {
    position: relative;
    padding: 5vw 0;

    &:before {
        content: " ";
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        border-radius: 0 1.5rem 1.5rem 0;
        width: calc(50% + 13rem);
        background: #fff;
    }

    &__left {
        width: 50%;
        padding-right: 6rem;
        position: relative;

        &__inner {
            position: relative;
        }
    }

    &__title {
        font-weight: 600;
        font-size: 3.5rem;
        line-height: 1.42;
    }

    &__btns {
        display: flex;
        gap: 2rem;

        > * {
            flex-grow: 1;
        }
    }

    &__right {
        position: absolute;
        width: calc(50% - 2rem);
        left: 50%;
        top: 50%;
        transform: translateY(-50%);
    }

    &__brands {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 3rem;
        margin-top: 4.8rem;
        margin-bottom: 6rem;
        height: 3.2rem;

        a {
            font-size: 0;
        }

        img {
            max-height: 3rem;
        }
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .section-pdf {
        padding: 0;

        &:before {
            display: none;
        }

        .page__center {
            max-width: none;
            padding: 0;
        }

        &__content,
        &__video {
            margin: 0 auto;
            max-width: 560px;
            padding: 0 2rem;
        }

        &__title {
            font-size: 2rem;

            span {
                display: block;
                font-size: 3rem;
            }
        }

        &__left {
            width: auto;
            padding: 2.2rem 0 4rem 0;
            background: #fff;
            border-radius: 0.5rem;
        }

        &__right {
            position: static;
            width: auto;
            transform: none;
        }

        &__btns {
            max-width: 30rem;
            margin: 0 auto;
            margin-top: 5rem;
            flex-direction: column;
            gap: 1.6rem 0;

            > * {
                text-transform: uppercase;
                justify-content: center;
            }
        }
    }
}

@media (max-width: 559.98px) {
    .section-pdf {
        &__video {
            padding: 0;
        }
    }
}

@media (max-width: 499.98px) {
    .section-pdf {
        &__brands {
            margin: 3.5rem -1.5rem;
            height: auto;
            justify-content: flex-start;
            flex-wrap: wrap;
            gap: 3rem 0;

            a {
                flex: 0 0 33.33%;
                padding: 0 1.5rem;
                text-align: center;
            }

            img {
                max-height: 3.5rem;
            }
        }
    }
}

@media (max-width: 359.98px) {
}
