.slider-history {
    position: relative;
    padding-top: 3.4rem;
    padding-bottom: 4.7rem;
    background: #204a8f;
    color: #fff;

    &__image {
        height: calc(100% - 14rem);
        position: absolute;
        bottom: 5.5rem;
        right: 2rem;
        opacity: 0.45;
    }

    &__title {
        margin-bottom: 2rem;
        font-weight: 600;
        font-size: 3.5rem;
    }

    &__about {
        margin-bottom: 2.5rem;
        font-size: 1.3rem;
        line-height: calc(17 / 13);

        li {
            list-style: disc inside;
        }
    }

    &__row {
        display: flex;
        flex-wrap: wrap;
        gap: 3.2rem 0;
    }

    &__col {
        flex: 0 0 auto;
        margin-right: 4.4rem;
        width: 37.22%;
        display: flex;
    }

    &__item {
        flex-grow: 1;
        background: rgba(217, 217, 217, 0.1);
        border: 1px solid #ffffff;
        backdrop-filter: blur(5px);
        border-radius: 11px;
        padding: 2.4rem 2.8rem;
    }

    &__year {
        margin-bottom: 1.5rem;
        font-weight: 600;
        font-size: 2.2rem;
    }

    &__desc {
        font-size: 1.4rem;
        line-height: calc(18 / 14);
    }

    &__arrows {
        position: relative;
        margin-top: 4.8rem;
        color: #fff;

        .arrows__btn-prev,
        .arrows__btn-next {
            font-size: 3.5rem;
        }
    }

    &__scroll {
        position: absolute;
        left: 50%;
        top: 100%;
        transform: translate(-50%, -50%);
        width: calc(50 / 30) + em;
        height: calc(68 / 30) + em;
        background: #204a8f;
        box-shadow: 0 0 0 calc(9 / 30) + em #f1f1f2;
        border-radius: 8rem;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 3rem;

        .icon {
            animation: 1s infinite scroll-down;
            font-size: 1em;
        }
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .slider-history {
        border-radius: 1.5rem;
        padding: 2rem 0;

        &__title {
            margin-bottom: 1.5rem;
            font-size: 2.5rem;
        }

        &__about {
            font-size: 1rem;
        }

        &__image {
            width: calc(100% - 4rem);
            height: auto;
            right: auto;
            left: 50%;
            max-width: 560px;
            transform: translateX(-50%);
            max-height: 40rem;
        }

        &__row {
            gap: 1.2rem;
        }

        &__col {
            width: 100%;
            display: block;
            margin: 0;
        }

        &__item {
            padding: 2rem;
        }

        &__year {
            font-size: 1.8rem;
        }

        &__desc {
            font-size: 1rem;
        }

        &__arrows {
            margin-top: 1.5rem;

            .arrows__btn-prev,
            .arrows__btn-next {
                font-size: 2rem;
            }

            .arrows__count {
                font-size: 1.5rem;
            }
        }

        &__scroll {
            font-size: 1.5rem;
        }
    }
}

