.table-default {
    min-width: 50rem;
    padding: 0.8rem 3.4rem;
    background: #fff;

    table {
        width: 100%;
    }

    th,
    td {
        padding: 2rem;
        font-weight: 500;
        font-size: 1.6rem;
        line-height: calc(19 / 16);
        text-align: left;
        color: #373737;

        &:first-child {
            padding-left: 0;
        }

        &:last-child {
            padding-right: 0;
        }
    }

    th {
        font-weight: 600;
    }

    td {
    }
}

