.list-docs {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -0.8rem;
    margin-top: -1.6rem;

    &__col {
        flex: 0 0 auto;
        padding: 0 0.8rem;
        width: 33.33%;
        margin-top: 1.6rem;
        display: flex;
    }

    &__item {
        flex-grow: 1;
        padding: 1.2rem 2rem;
        display: flex;
        align-items: center;
        gap: 1.2rem;
        font-weight: 500;
        font-size: 1.6rem;
        line-height: calc(20 / 16);
        color: #1a1919;

        &:hover {
            background: #f5f5f5;
            border-radius: 0.5rem;
            color: #204a8f;

            .list-docs__icon-download,
            .list-docs__icon-link {
                background: currentColor;
            }
        }
    }

    &__name {
    }

    &__icon-download,
    &__icon-link {
        transition: all 0.2s;
        flex: 0 0 auto;
        font-size: 1em;
        width: 1em;
        height: 1em;
    }

    &__icon-download {
        background: #000000;
        mask: url(../img/icons/download.svg) no-repeat 50% 50%;
        margin-left: auto;
    }

    &__icon-link {
        background: #9a9a9a;
        mask: url(../img/icons/link.svg) no-repeat 50% 50%;
    }
}

