.card-article {
    transition: all 0.2s;
    background: #ffffff;
    border-radius: 1rem;
    padding: 3rem;

    &:hover {
        box-shadow: 1px 7px 10px rgba(0, 0, 0, 0.25);
    }

    &__image {
        position: relative;
        margin: -1rem -3rem 5.4rem -3rem;
        display: block;

        &:before {
            content: " ";
            padding-top: 35.48%;
            display: block;
        }

        img {
            @include fill;
            object-fit: cover;
        }
    }

    &__title {
        margin-bottom: 3.4rem;
        font-weight: 500;
        font-size: 1.9rem;
        line-height: calc(23 / 19);
        color: #333333;
    }

    &__footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 2rem;
    }

    &__time {
        display: block;
        font-size: 1.2rem;
        line-height: 1;
        color: #999999;
    }

    &__tags {
        display: flex;
        flex-wrap: wrap;
        gap: 1rem;

        &__item {
            font-weight: 600;
            font-size: 1.2rem;
            line-height: calc(23 / 12) + em;
            text-transform: uppercase;
            color: #204a8f;
            border: 1px solid #204a8f;
            border-radius: 4rem;
            background: #fff;
            padding: 0 1.5rem;
        }
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .card-article {
        padding: 2.5rem;

        &__image {
            margin: -1rem -2.5rem 4.7rem -2.5rem;
        }

        &__tags {
            &__item {
                font-size: 1rem;
            }
        }
    }
}

