.l-faq {
    &__search {
        margin-top: 3rem;
    }

    &__tags {
        margin-top: 4.3rem;
        margin-bottom: 3.7rem;
        max-width: 80rem;
    }

    &__types {
        margin-top: 4rem;
        margin-bottom: 2.4rem;
    }

    &__list {
        max-width: 80rem;
    }

    &__pages {
        margin-top: 6.3rem;
        margin-bottom: 12rem;
    }
}

@media (max-width: 1299.98px) {
    .l-faq {
        &__pages {
            margin-top: 3rem;
            margin-bottom: 4rem;
            display: flex;
            justify-content: center;
        }
    }
}

