.btn-copy-sm {
    padding: calc(5 / 12) + em calc(7 / 12) + em;
    background: #204a8f;
    border-radius: 0.5rem;
    font-weight: 500;
    font-size: 1.2rem;
    white-space: nowrap;
    line-height: calc(14 / 12);
    color: #f2f2f2;

    &:hover {
        background: darken(#204a8f, 5%);
    }

    .icon {
        margin-left: calc(5 / 14) + em;
        font-size: calc(14 / 12) + em;
        fill: currentColor;
    }

    &--text {
        padding: 0;
        background: none !important;
        color: #204a8f;

        &:hover {
            color: darken(#204a8f, 5%);
        }
    }
}
