.l-catalog-section {
    &__total {
        margin-top: 2.3rem;
        margin-bottom: 2.7rem;
        font-weight: 500;
        font-size: 1.6rem;
        line-height: calc(20 / 16);
        color: #9a9a9a;
    }

    &__categories {
        margin: 3rem 0;
    }

    &__search {
        margin: 3rem 0;
    }

    &__main {
        margin-bottom: 9rem;
    }

    &__pages {
        margin-top: 5rem;
    }
}

@media (max-width: 1299.98px) {
    .l-catalog-section {
        &__total {
            margin: 1.6rem 0;
        }

        &__main {
            margin-bottom: 4rem;
        }

        &__pages {
            margin-top: 3rem;
            display: flex;
            justify-content: center;
        }
    }
}
