.article-default {
    font-size: 1.8rem;
    line-height: calc(30 / 18);
    color: #1a1919;

    &__promo-link {
        a {
            font-weight: 600;
            font-size: 2.5rem;
            line-height: calc(28 / 25);
            text-decoration: underline;
        }
    }

    &__intro-text {
        font-size: 2rem;
    }

    section {
        margin: 6rem 0;

        @include reset-margins;
    }

    h2 {
        margin-bottom: 2rem;
        font-weight: 600;
        font-size: 2.5rem;
        line-height: calc(20 / 25);
        color: #204a8f;
    }

    h3 {
        margin-bottom: 2rem;
        font-weight: 600;
        font-size: 2rem;
        line-height: 1;
        color: #204a8f;
    }

    h4 {
        font-weight: 500;
        font-size: 2rem;
    }

    b {
        font-weight: 500;
    }

    small {
        display: block;
        margin-top: 3rem;
        font-size: 0.7em;
    }

    ol {
        > ol {
            padding-left: 2rem;
        }
    }

    ul {
        padding-left: 2rem;

        li {
            list-style: disc;
        }
    }

    ul,
    p {
        margin: 1.5rem 0;

        @include reset-margins;
    }

    a {
        text-decoration: underline;
    }
}
