.contacts {
    position: relative;

    &.opened {
        .contacts__btn-toggle {
            margin-top: 33.5rem;
        }
    }

    &__map {
        height: 46rem;
        position: absolute;
        left: 0;
        width: 100%;
        bottom: 2.5rem;
        background: #dfdfdf;
        overflow: hidden;
    }

    &__content {
    }

    &__center {
        position: relative;
        max-width: 93rem;
        margin: 0 auto;
    }

    &__image {
        @include fill;
        background: no-repeat 100% 50% / cover;
        border-radius: 1.5rem 0 0 1.5rem;
    }

    &__panels {
        display: flex;
    }

    &__panel {
        transition: all 0.2s;
        flex: 0 0 auto;
        width: 32rem;
        display: flex;

        > * {
            flex-grow: 1;
        }

        &--desc {
            position: relative;
            z-index: 10;
        }

        &--form {
            &.hidden {
                transform: translateX(-100%);
            }
        }
    }

    &__btn-toggle {
        margin-top: 10.6rem;
        display: flex;
        justify-content: center;
        position: relative;

        button {
            min-width: 22rem;
        }
    }
}

@media (max-width: 1299.98px) {
    .contacts {
        &.opened {
            .contacts__btn-toggle {
                margin-top: 18.5rem;
            }
        }
        &__map {
            height: 18rem;
        }

        &__image {
            position: static;
            border-radius: 1rem;
            margin-bottom: 2rem;
            height: 19rem;
        }

        &__panels {
            overflow: hidden;
            border-radius: 1.5rem;
        }

        &__panel {
            width: 100%;

            &--form {
                transform: translateX(-100%);
                z-index: 10;

                &.hidden {
                    transform: translateX(-200%);
                }
            }
        }

        &__btn-toggle {
            margin-top: 5.4rem;

            button {
                font-size: 1.5rem;
            }
        }
    }
}

