.form-search-points {
    display: flex;
    align-items: center;
    gap: 4.8rem;

    &__links {
        display: flex;
        align-items: center;
        gap: 4.8rem;
        margin-right: auto;

        a {
            font-size: 1.6rem;
            line-height: calc(24 / 16);
            color: #2e2a25;

            &.active {
                font-weight: 500;
                color: #204a8f;
            }
        }
    }

    &__select {
        flex-grow: 1;
    }

    &__input {
        flex-grow: 1;
    }
}

@media (max-width: 1299.98px) {
    .form-search-points {
        flex-wrap: wrap;
        gap: 1rem 3rem;

        &__links {
            flex-wrap: wrap;
            gap: 1rem 3rem;
        }
    }
}

@media (max-width: 599.98px) {
    .form-search-points {
    }
}
