.l-catalog-filter {
    &__categories {
        margin: 3rem 0;
    }

    &__search {
        margin: 3rem 0;
    }

    &__main {
        display: flex;
        margin-bottom: 9rem;

        &__left {
            flex: 0 0 auto;
            width: 27.5rem;
        }

        &__right {
            flex-grow: 1;
            padding-left: 4.5rem;
            min-width: 0;
        }

        &__catalog {
            margin-top: 2.5rem;
        }
    }

    &__filter {
        &__btn-toggle {
            display: none;
        }
    }

    &__pages {
        margin-top: 5rem;
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .l-catalog-filter {
        &__search {
            margin-top: 2.4rem;
            margin-bottom: 1.5rem;
        }

        &__main {
            display: block;
            margin-bottom: 4rem;

            &__left {
                width: auto;
            }

            &__right {
                padding: 0;
                margin-top: 3rem;
            }

            &__catalog {
                display: none;
            }
        }

        &__filter {
            &__btn-toggle {
                display: block;
                margin-bottom: 1rem;
            }

            &__collapse {
            }
        }
    }
}

@media (max-width: 599.98px) {
    .l-catalog-filter {
    }
}
