.btn-outline {
    padding: calc(14 / 13) + em calc(24 / 13) + em;
    border: 1px solid #204a8f;
    border-radius: 0.5rem;
    font-weight: 600;
    font-size: 1.3rem;
    line-height: 1.2;
    text-align: center;
    color: #204a8f;

    &:hover {
        background: #204a8f;
        color: #fff;
    }

    &--border-2 {
        box-shadow: inset 0 0 0 1px #204a8f;
    }

    &--size-2 {
        padding: calc(15 / 17) + em calc(20 / 17) + em;
        font-size: 1.7rem;
    }
}

@media (max-width: 1299.98px) {
    .btn-outline {
        font-size: 1.1rem;

        &--size-2 {
            font-size: 1.4rem;
        }
    }
}

@media (max-width: 599.98px) {
}
