.btn-expand {
    justify-content: space-between;
    gap: 3rem;
    padding: calc(13 / 14) + em calc(12 / 14) + em;
    padding-right: calc(32 / 14) + em;
    background: #ffffff;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
    border-radius: 0.5rem;
    font-weight: 500;
    font-size: 1.4rem;
    line-height: calc(20 / 14);
    color: #757575;

    &.active {
        color: #204a8f;

        .icon {
            transform: rotate(90deg);
            stroke: #204a8f;
        }
    }

    .icon {
        transition: all 0.2s;
        font-size: 1.2rem;
        stroke: #525252;
    }
}
