.card-product {
    position: relative;
    display: flex;
    flex-direction: column;

    &--panel {
        border-radius: 1rem;
        padding: 1.3rem;

        &:hover {
            background: #fff;
        }
    }

    &__image {
        position: relative;
        background: #f3f3f3;
        border-radius: 1rem;
        overflow: hidden;

        &:before {
            content: " ";
            display: block;
            padding-top: 86%;
        }

        img {
            @include fill;
            object-fit: cover;
        }

        &--square {
            margin-right: 2rem;

            &:before {
                padding-top: 100%;
            }
        }
    }

    &__name {
        margin-top: 1.4rem;
        margin-bottom: auto;
        font-weight: 500;
        font-size: 1.4rem;
        line-height: calc(14 / 12);
    }

    &__ctrl {
        margin-top: 2.6rem;
        display: flex;
        align-items: center;
        gap: 0.8rem;
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .card-product {
        padding: 0;

        &:hover {
            background: none;
        }

        &__image {
            margin-right: 2rem;

            &:before {
                padding-top: 100%;
            }
        }

        &__name {
            margin-top: 0.8rem;
            font-size: 1.2rem;
        }

        &__ctrl {
            margin-top: 1.2rem;
        }
    }
}

