.nav-cats {
    ul {
        display: flex;
        flex-wrap: wrap;
        gap: 2.2rem 2rem;
    }

    li {
        &.active {
            a {
                background: #204a8f;
                color: #fff;
            }
        }
    }

    a {
        border-radius: 0.4rem;
        background: #fff;
        display: block;
        padding: 0.3rem 1.7rem;
        font-size: 1.5rem;
        line-height: calc(22 / 15);
        color: #272c2e;
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .nav-cats {
        ul {
            gap: 1.5rem 2rem;
        }
    }
}

