.panel-attn {
    min-height: 7.6rem;
    padding: 0 2.7rem;
    background: #ffffff;
    border-radius: 0.5rem;
    display: inline-flex;
    align-items: center;
    gap: 3rem;

    &__text {
        flex-grow: 1;
        font-weight: 500;
        font-size: 1.5rem;
        line-height: calc(20 / 15);
        color: #37383e;
    }

    &__icon {
        transform: translateY(10%);
        .icon {
            font-size: 6.5rem;
        }
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .panel-attn {
        padding: 1rem 1.8rem;
        flex-direction: row-reverse;

        &__text {
            font-size: 1.3rem;
        }

        &__icon {
            .icon {
                font-size: 6rem;
            }
        }
    }
}

