.list-oems {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -1.4rem;
    gap: 2.8rem 0;

    &__group {
        flex: 0 0 auto;
        width: 50%;
        padding: 0 1.4rem;
    }

    &__row {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -0.7rem;
        gap: 1.4rem 0;

        > div {
            flex: 0 0 auto;
            width: 33.33%;
            padding: 0 0.7rem;

            span {
                background: #ffffff;
                border-radius: 0.5rem;
                padding: 1.6rem 0;
                display: block;
                font-weight: 500;
                font-size: 1.5rem;
                line-height: calc(18 / 15);
                text-align: center;
                color: #000000;
            }
        }
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .list-oems {
        &__group {
            width: 100%;
        }

        &__row {
            > div {
                span {
                    color: #525252;
                    border-radius: 1.5rem;
                    font-size: 1rem;
                    padding: 0.7rem 0;
                    line-height: 1.2;
                }
            }
        }
    }
}

