.btn-header {
    width: 100%;
    padding: 1.2rem 0;
    border-bottom: 1px solid #afafaf;
    justify-content: space-between;
    font-weight: 500;
    font-size: 2rem;
    line-height: calc(24 / 20);
    color: #204a8f;

    &.opened {
        .icon {
            transform: rotate(270deg);
        }
    }

    .icon {
        transition: all 0.2s;
        transform: rotate(90deg);
        margin: calc(5 / 15) + em;
        font-size: calc(15 / 20) + em;
        stroke: #204a8f;
    }
}

