.nav-docs {
    ul {
        padding: 0.5rem 0;
    }

    li {
        padding: 1rem 0;
        font-weight: 500;
        font-size: 2rem;
        line-height: calc(24 / 20);
        color: #1a1919;

        &:hover,
        &.active {
            color: #204a8f;
        }
    }

    a {
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .nav-docs {
        ul {
            padding: 0;
        }

        li {
            padding: 0;
        }

        a {
            display: block;
            padding: 1.2rem 3.2rem;
            border-bottom: 1px solid #f5f5f5;
            border-radius: 0.5rem;
            font-size: 1.6rem;
            line-height: calc(20 / 16);
        }
    }
}

