.list-goods-few {
    &__header {
        margin-bottom: 2.4rem;
    }
    &__list {
        position: relative;
    }

    &__row {
        display: flex;
    }

    &__col {
        flex: 0 0 auto;
        padding-right: 2rem;
        width: 33.33%;
    }

    &__count {
        position: absolute;
        top: 0;
        right: 2rem;
        padding: 0.4rem;
        min-width: 4rem;
        background: #ffffff;
        box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
        border-radius: 0.5rem;
        font-weight: 500;
        font-size: 1.5rem;
        line-height: calc(18 / 15);
        text-align: center;
        color: #204a8f;
    }
}
