.promo-about {
    position: relative;
    height: 57rem;
    padding: 1.7rem 2.7rem;
    border-radius: 1.3rem;
    background: #ffffff;

    &__ellipse {
        @include fill;
        border-radius: 1.3rem;
        overflow: hidden;

        &:before {
            content: " ";
            width: 200%;
            height: 50%;
            background: #204a8f;
            display: block;
            border-radius: 50%;
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translate(-50%, 33%) rotate(-12deg);
        }
    }

    &__header {
        &__btn-more {
        }
    }

    &__title {
        margin-bottom: 1.2rem;
        font-weight: 600;
        font-size: 2.5rem;
        line-height: 1;
        color: #204a8f;
    }

    &__year {
        width: 15.4rem;
        position: absolute;
        z-index: 10;
        bottom: 3rem;
        right: 2.7rem;
    }

    &__image {
        position: absolute;
        width: 100%;
        right: 0;
        top: 50%;
        transform: translate(2rem, -52%);
        max-width: 40rem;
    }

    &__btn-more {
        display: none;
    }
}

@media (max-width: 1299.98px) {
    .promo-about {
        height: 16rem;

        &__ellipse {
            &:before {
                width: 200%;
                height: 90%;
                transform: translate(-50%, 50%) rotate(-4.5deg);
            }
        }

        &__header {
            &__btn-more {
                display: none;
            }
        }

        &__title {
            font-size: 1.3rem;
        }

        &__image {
            max-width: 22rem;
            transform: translate(-1rem, -52%);
        }

        &__year {
            width: 5.7rem;
        }

        &__btn-more {
            display: block;
            position: absolute;
            bottom: 1.5rem;
            left: 1rem;

            button,
            a {
                font-size: 1.3rem;
            }
        }
    }
}
