.slider-stories {
    &__header {
        display: flex;
        align-items: center;
        margin-bottom: 3rem;
    }

    &__title {
    }

    &__btns {
        margin-left: auto;
    }

    &__crop {
        overflow: hidden;
    }

    &__slider {
        margin: 0 -0.7rem;

        .swiper-slide {
            width: 16.66%;
            padding: 0 0.7rem;
        }
    }

    &__image {
        transition: all 0.2s;
        position: relative;
        display: block;
        border-radius: 1.6rem;
        overflow: hidden;

        &--in-place,
        body > & {
            &:hover {
                transform: scale(1.15);
                box-shadow: 0 0 0 0.66rem #204a8f;

                &:before {
                    opacity: 0;
                }
            }
        }

        &:before {
            transition: all 0.2s;
            content: " ";
            @include fill;
            z-index: 10;
            background: #000;
            opacity: 0.4;
        }
    }

    &__video {
        @include fill;
        object-fit: cover;
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .slider-stories {
        &__header {
            margin-bottom: 5rem;
        }

        .swiper-slide {
            width: 25%;
        }
    }
}

@media (max-width: 599.98px) {
    .slider-stories {
        &__crop {
            overflow: visible;
        }

        &__slider {
            overflow: visible;

            .swiper-slide {
                width: 10rem;
            }
        }
    }
}

