.page {
    overflow: hidden;
    display: flex;
    flex-direction: column;
    min-height: 100%;

    &__locked {
        overflow: hidden;
    }

    &__content {
        flex-grow: 1;
    }

    &__center {
        padding: 0 var(--side-padding);
        box-sizing: content-box;
        max-width: 1190px;
        margin: 0 auto;
    }

    &__offset {
        padding-left: 2.5rem;
    }

    &__h1 {
        font-weight: 600;
        font-size: 3.5rem;
        line-height: calc(44 / 35);
    }

    &__h2 {
        font-weight: 600;
        font-size: 3rem;
        line-height: calc(29 / 25);
        color: #1a1919;
    }

    &__h3 {
        font-weight: 600;
        font-size: 2.5rem;
        line-height: calc(29 / 25);
        color: #1a1919;
    }

    &__h4 {
        font-weight: 600;
        font-size: 1.9rem;
        line-height: calc(22 / 19);
    }

    &__date {
        font-size: 1.8rem;
        color: #9a9a9a;
    }

    &__tag {
        padding: 0.3rem 1.7rem;
        background: #204a8f;
        border-radius: 0.4rem;
        font-size: 1.5rem;
        line-height: calc(22 / 15);
        color: #ffffff;
    }
}

@media (max-width: 1299.98px) {
    .page {
        &__center {
            max-width: 560px;
        }

        &__h1 {
            font-size: 2.5rem;
        }

        &__h2,
        &__h3 {
            font-size: 2rem;
        }

        &__offset {
            padding-left: 1rem;
        }
    }
}

@media (max-width: 599.98px) {
    .page {
        &__center {
            max-width: none;
        }
    }
}
