.form-search-faq {
    display: flex;

    &__left {
        flex-grow: 1;
        max-width: 80rem;
        display: flex;
        gap: 2.6rem;
    }

    &__right {
        margin-left: auto;
    }

    &__select {
        flex-grow: 1;
        max-width: 28.5rem;
    }

    &__btn-search {
        margin-left: auto;

        button {
            min-width: 14.5rem;
        }
    }
}

@media (max-width: 1299.98px) {
    .form-search-faq {
        display: block;

        &__left {
            display: block;
        }

        &__select {
            max-width: none;
            margin-top: 1rem;
        }

        &__btn-search,
        &__right {
            margin-top: 1rem;

            button {
                width: 100%;
            }
        }
    }
}

@media (max-width: 599.98px) {
    .form-search-faq {
    }
}
