.table-markets {
    width: 100%;

    tr {
    }

    th,
    td {
        padding: 1.6rem 3.4rem;
        text-align: left;
        font-size: 1.6rem;
        line-height: calc(19 / 16);

        &:last-child {
            padding-left: 0;
        }
    }

    th {
        color: #828e9b;
    }

    td {
        border-top: 1px solid #e7e7e7;
        color: #272c2e;
    }

    a {
        text-decoration: underline;
        color: #204a8f;
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .table-markets {
        th,
        td {
            padding-right: 2rem;
            padding-left: 2rem;
        }
    }
}

