.nav-count {
    font-size: 1.5rem;
    line-height: calc(20 / 15);
    color: #272c2e;

    ul {
        display: flex;
        margin-right: -5rem;
        flex-wrap: wrap;
        gap: 2rem 0;
    }

    li {
        margin-right: 5rem;
    }

    a {
        display: flex;
        align-items: center;
        gap: 1rem;

        .icon {
            font-size: calc(20 / 15) + em;
            fill: #828e9b;
        }

        span + span {
            color: #828e9b;
        }
    }
}

@media (max-width: 1299.98px) {
    .nav-count {
        ul {
            gap: 1.4rem 0;
        }

        li {
            margin-right: 3rem;
        }

        a {
            .icon {
                display: none;
            }
        }
    }
}

@media (max-width: 599.98px) {
    .nav-count {
    }
}
