.card-section-links {
    position: relative;
    background: #ffffff;
    border-radius: 1.5rem;
    overflow: hidden;
    min-height: 37rem;

    &__circle {
        width: 70%;
        height: 170%;
        border-radius: 50%;
        background: #204a8f;
        position: absolute;
        left: 0;
        top: 0;
        transform: translate(-30%, -30%);
    }

    &__image {
        position: absolute;
        right: 50%;
        left: 6%;
        top: 2.5rem;
        bottom: 2.5rem;
        background: no-repeat 50% 50% / contain;
    }

    &__desc {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 50%;
        right: 0;
        padding: 0 7.5rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    &__title {
        margin-bottom: 1.3rem;
        font-weight: 600;
        font-size: 2.2rem;
        line-height: calc(20 / 22);
        color: #204a8f;
    }

    &__nav {
        ul {
        }

        li {
            margin: 1.8rem 0;
        }

        a {
            display: inline-flex;
            gap: 1rem;
            font-size: 1.5rem;
            line-height: calc(20 / 15);
            color: #272c2e;
        }

        span {
            color: #828e9b;
        }
    }
}
