.form-search-catalog {
    display: flex;
    gap: 1.5rem;

    &__left {
        flex: 0 0 auto;
        width: 21%;
    }

    &__right {
        flex-grow: 1;
    }

    &__input {
        margin-bottom: 1.7rem;
    }

    &__label {
        margin-bottom: 1.2rem;
        font-weight: 500;
        font-size: 1.6rem;
        line-height: calc(20 / 16);
        color: #757575;
    }

    &__filter {
        display: flex;
        flex-wrap: wrap;
        gap: 1.5rem;
    }

    &__select {
        flex-grow: 1;
        max-width: 25rem;
    }

    &__button {
        button {
            min-width: 10.7rem;
        }
    }
}

@media (max-width: 1299.98px) {
    .form-search-catalog {
        &__left {
            width: 100%;
            background: #ffffff;
            box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
            border-radius: 0.5rem;
            padding: 1rem;
        }

        &__right {
            display: none;
        }

        &__label {
            margin-bottom: 1rem;
            font-size: 1.3rem;
        }

        &__input {
            margin-bottom: 0.5rem;
        }
    }
}

@media (max-width: 599.98px) {
    .form-search-catalog {
    }
}
