.l-catalog {
    &__search {
        margin: 3rem 0;
    }

    &__main {
        display: flex;
        margin-bottom: 14rem;

        &__left {
            flex: 0 0 auto;
            width: 31rem;
        }
        &__right {
            flex: 0 0 auto;
            width: calc(100% - 31rem);
            padding-left: 5.5rem;
        }
    }

    &__content {
        margin: 5rem 0;
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .l-catalog {
        &__main {
            display: block;
            margin-bottom: 3rem;

            &__left {
                width: auto;
            }

            &__right {
                display: none;
            }
        }

        &__search-sm {
            margin: 1.8rem 0 2.4rem 0;
        }
    }
}

