.section-tires {
    position: relative;
    height: 56.5rem;
    display: flex;
    justify-content: center;
    flex-direction: column;

    &__image {
        @include fill;
        background: url(../img/section-tires/1.jpg) no-repeat 0 50%;
    }

    &__center {
        position: relative;
        z-index: 10;
        text-align: right;
        padding: 0 10rem;
        width: 100%;
        max-width: 2200px;
        margin: 0 auto;
    }

    &__logo {
        margin-top: -2rem;
        display: inline-block;
        width: 16.5rem;
    }

    &__desc {
        margin-top: 3.4rem;
        font-weight: 500;
        font-size: 2.5rem;
        line-height: 1.35;
        color: #204a8f;
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .section-tires {
        height: auto;

        &__center {
            max-width: 560px;
            text-align: left;
            padding: 4.2rem 2rem;
        }

        &__logo {
            margin-top: 0;
            width: 9.2rem;
        }

        &__desc {
            margin-top: 2.5rem;
            font-size: 1.8rem;
        }

        &__image {
            position: static;
            height: 30rem;
            background-image: url(../img/section-tires/1--mobile.jpg);
            background-size: auto 100%;
            margin-top: -13vw;
            margin-bottom: 3.5rem;
        }
    }
}

@media (max-width: 599.98px) {
    .section-tires {
        &__image {
            margin-top: -11rem;
        }
    }
}

