.nav-pages {
    display: flex;
    align-items: center;

    > * {
        margin-right: 0.4rem;

        &:last-child {
            margin-right: 0;
        }
    }

    &__page {
        padding: 1rem;
        font-size: 1.3rem;
        color: #1a1919;

        &.active {
            color: #003e7b;
        }
    }

    &__input {
        padding: 0 1rem;
        width: 7rem;
    }

    &__next {
        padding: 1rem;

        .icon {
            font-size: 0.9rem;
        }
    }
}
