.slider-products {
    display: flex;

    &__left {
        padding: 2.5rem 5.5rem 0 2.5rem;
    }

    &__right {
        overflow: hidden;
        flex-grow: 1;
    }

    &__btns {
        margin-top: 11rem;
    }

    &__slider {
        margin: 0 -0.2rem;

        .swiper-wrapper {
        }

        .swiper-slide {
            width: 25%;
            padding: 0 0.2rem;
            height: auto;
            display: flex;

            > * {
                flex-grow: 1;
            }
        }
    }
}

@media (min-width: 1300px) {
}

@media (max-width: 1299.98px) {
    .slider-products {
        display: block;

        &__left {
            margin-bottom: 2rem;
            padding: 0 0 0 1rem;
        }

        &__btns {
            display: none;
        }

        &__title {
            font-size: 2rem;
        }

        &__slider {
            margin: 0 -1rem;

            .swiper-slide {
                width: 33.33%;
                padding: 0 1rem;
            }
        }
    }
}

@media (max-width: 599.98px) {
    .slider-products {
        &__right {
            overflow: visible;
        }

        &__slider {
            overflow: visible;

            .swiper-slide {
                display: block;
                width: 22rem;
            }
        }
    }
}

