.btn-arrow {
    gap: 1em;
    padding: calc(10 / 17) + em calc(15 / 17) + em;
    border: 1px solid #204a8f;
    border-radius: 0.5rem;
    background: #ffffff;
    font-weight: 600;
    font-size: 1.7rem;
    line-height: calc(20 / 17);
    color: #204a8f;

    &--gray-border {
        border-color: #d0d0d0;

        .icon {
            color: #9a9a9a;
        }
    }

    &--gray-bg {
        background: #e8e9e9;
        border-color: transparent;
        font-weight: 400;
        color: #212529;

        &:hover {
            background: darken(#e8e9e9, 5%);
        }
    }

    &--center {
        justify-content: center;

        .icon {
            margin-left: 0 !important;
        }
    }

    &--size-1 {
        padding: calc(11 / 12) + em calc(15 / 12) + em;
        font-size: 1.2rem;
        line-height: 1;
    }

    .icon {
        margin-left: auto;
        stroke: currentColor;
        font-size: calc(14 / 17) + em;
    }
}
