.input-search-shadow {
    position: relative;
    font-size: 1.8rem;

    input {
        width: 100%;
        padding: calc(13 / 18) + em calc(25 / 18) + em;
        padding-right: 4em;
        border: 1px solid #efefef;
        box-shadow: 2px 1px 6px rgba(0, 0, 0, 0.15);
        border-radius: 0.5rem;
        font-family: inherit;
        font-weight: 500;
        font-size: 1em;
        line-height: calc(24 / 18);
        color: currentColor;
        outline: none;

        &::placeholder {
            color: #afafaf;
        }
    }

    .icon {
        position: absolute;
        top: 50%;
        right: calc(25 / 18) + em;
        transform: translateY(-50%);
        font-size: calc(20 / 18) + em;
        fill: #204a8f;
    }
}
