.nav-catalog {
    position: relative;
    height: 81rem;

    &__box {
        position: absolute;
        display: flex;
        flex-direction: column;

        &--1 {
            top: 0;
            left: 0;
            width: 30%;
            height: 31.6rem;
            padding-right: calc(2.4rem + 2.4rem + 1.6rem);
        }
        &--2 {
            top: 0;
            left: calc(30% - 1.6rem - 2.4rem);
            width: 30%;
            height: 31.6rem;
        }
        &--3 {
            top: 0;
            left: calc(60% - 1.6rem);
            width: calc(40% + 1.6rem);
            height: 31.6rem;
        }
        &--4 {
            top: 34rem;
            left: 0;
            width: 30%;
            height: 47rem;
            padding-right: 2.4rem;
        }
        &--5 {
            top: 34rem;
            left: 30%;
            width: 30%;
            height: 47rem;
        }
        &--6 {
            top: 34rem;
            left: calc(60% + 2.4rem);
            height: 26.4rem;
            width: 20%;
        }
        &--7 {
            top: 62.8rem;
            left: calc(60% + 2.4rem);
            width: calc(40% - 2.4rem);
            height: 18.2rem;
        }
        &--8 {
            top: 34rem;
            left: calc(80% + 4.8rem);
            right: 0;
            height: 28.8rem;
        }
    }

    &__more {
        margin-bottom: 2.4rem;
        height: 10rem;
        padding: 2.3rem;
        padding-right: 5.7rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 3rem;
        font-weight: 600;
        font-size: 2.9rem;
        background: #204a8f;
        border-radius: 2rem;
        color: #fff;

        .icon {
            font-size: calc(18 / 30) + em;
            stroke: currentColor;
        }
    }

    &__item {
        position: relative;
        background: #ffffff;
        border-radius: 2rem;
        flex-grow: 1;
        display: flex;
        flex-direction: column;

        &--7 {
            flex-direction: row;
            border-top-right-radius: 0;

            .nav-catalog__image {
                margin-top: 0;
            }
        }

        &--8 {
            border-radius: 2rem 2rem 0 0;
        }

        &--blue {
            background: #204a8f;

            .nav-catalog__name {
                color: #fff;
            }
        }
    }

    &__name {
        padding: 1.7rem 2.3rem;
        font-weight: 600;
        font-size: 2.9rem;
        color: #1a1919;
    }

    &__image {
        background: no-repeat 100% 0 / contain;
        flex: 1 1 auto;
        max-height: 31rem;
        margin-top: auto;
    }

    &__bridge {
        height: 10rem;
        position: absolute;
        left: 0;
        bottom: -6rem;
        background: #fff;
        width: 1.6rem;

        &--blue {
            background: #204a8f;
        }
    }

    &__angle {
        position: absolute;
        top: -1.7rem;
        right: 1.6rem;
        width: 1.8rem;
        height: 1.8rem;
        background: #204a8f;
        mask: url(../img/curved-angle.svg) no-repeat 50% 50%;

        &--white {
            background: #fff;
        }
        &--bottom {
            top: auto;
            bottom: -1.7rem;
            transform: scale(-1);
            right: auto;
            left: 1.6rem;
        }
    }
}

@media (max-width: 1299.98px) {
    .nav-catalog {
        position: relative;
        height: 49rem;

        &__name {
            padding: 1.3rem;
            padding-bottom: 0;
            font-size: 1.3rem;

            br {
                display: none;
            }
        }

        &__more {
            height: 4.5rem;
            border-radius: 1rem;
            padding: 1.3rem;
            padding-right: 2.2rem;
            margin-bottom: 1rem;
            font-size: 1.3rem;
        }

        &__box {
            &--1 {
                width: 50%;
                height: 17rem;
                padding-right: 1.7rem;
            }

            &--2 {
                top: 0;
                right: 0;
                left: auto;
                width: calc(50% + 0.7rem);
                height: 13rem;
            }

            &--3 {
                top: 30.6rem;
                left: 0;
                width: 50%;
                height: 18.5rem;

                .nav-catalog__bridge,
                .nav-catalog__angle {
                    display: none;
                }
            }

            &--4 {
                top: 18rem;
                left: 0;
                width: 50%;
                height: 11.5rem;
                padding: 0;
            }

            &--5 {
                left: 50%;
                top: 140px;
                width: 50%;
                height: 15.5rem;
                padding: 0;
                padding-left: 1rem;

                .nav-catalog__bridge,
                .nav-catalog__angle {
                    display: none;
                }
            }

            &--6 {
                left: calc(50% + 1rem);
                width: calc(50% - 1rem);
                height: 9rem;
                top: 40rem;
            }

            &--7 {
                top: 30.6rem;
                left: calc(50% + 1rem);
                width: calc(50% - 1rem);
                height: 8.5rem;
            }

            &--8 {
                display: none;
            }
        }

        &__item {
            border-radius: 1rem;

            &--6,
            &--7 {
                .nav-catalog__image {
                    position: absolute;
                    height: 80%;
                    width: 50%;
                    right: 0;
                    bottom: 0;
                }
            }
        }

        &__bridge {
            width: 0.7rem;
        }

        &__angle {
            right: 0.7rem;

            &--bottom {
                left: 0.7rem;
            }
        }

        &__image {
            max-height: 10rem;
        }
    }
}
