.panel-contacts {
    background: #ffffff;
    border: 1px solid #ededed;
    box-shadow: 0px 17px 16px rgba(235, 241, 245, 0.55);
    border-radius: 1.5rem;
    padding: 3rem;
    padding-top: 2rem;

    &__item {
        font-size: 1.6rem;
        line-height: calc(24 / 16);
        margin-bottom: 0.5rem;

        @include reset-margins;
    }

    &__label {
        margin-bottom: 0.2rem;
        color: #a0a5b9;
    }

    &__desc {
    }

    &__btns {
        margin-top: 1.2rem;
        display: flex;
        flex-direction: column;
        gap: 0.8rem;
    }
}

@media (max-width: 1299.98px) {
    .panel-contacts {
        padding: 2.4rem;
        padding-top: 1.8rem;

        &__item {
            font-size: 1.5rem;
        }
    }
}

@media (max-width: 599.98px) {
}
