.nav-models {
    ul {
        display: flex;
        gap: 1.1rem;
    }

    li {
        &.active {
            a {
                background: #204a8f;
            }
        }
    }

    a {
        display: block;
        min-width: calc(74 / 12) + em;
        background: #c1c1c1;
        border-radius: 0.5rem;
        padding: calc(7 / 12) + em calc(16 / 12) + em;
        font-weight: 500;
        font-size: 1.2rem;
        line-height: calc(14 / 12);
        text-align: center;
        color: #fcfcfc;
    }
}
